import React from "react";
import MyButton from "../ui/button/MyButton";
import MyInput from "../ui/input/MyInput";
import {Basic} from "../module/Dropzone/Basic"

const EditingUserView = ({onLogout, setNick,  newNick, setNewNick}) => {
    

    return (
        <>  
            <h3>  </h3>
            <div style={{display: "flex", flexDirection: "column", alignItems: "center", gap: "8px"}}>
                <div>
                    <Basic texInsideBlock={" "}></Basic>
                </div>
                <div>
                    <MyInput 
                        placeholder=""
                        value={newNick}
                        onChange={(e) => setNewNick(e.target.value)}
                    ></MyInput>
                    <MyButton onClick={setNick}> </MyButton>
                </div>
                <div>
                    <MyInput 
                        placeholder=" "
                    ></MyInput>
                    <MyButton > </MyButton>
                
                </div>
                <MyButton onClick={onLogout}></MyButton>
            </div>

        </>
    )
}

export default EditingUserView